.navActive {
  background-color: var(--tertiary);
}

header {
  flex-direction: row;
  display: inline;
  list-style-type: none;
  justify-content: space-between;
  font-size: 1.5rem;
  padding: .5rem;
}

nav {
  display: inline;
  list-style-type: none;
  align-content: right;
}

ul {
  list-style-type: none;
}

img {
  position:relative;
}

.column {
  float: left;
  width: 33.33%;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}

.skills li {
  padding: 1rem;
}
#resume a {
  padding: 2.5rem;
  margin: 2.5rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
  color: var(--tertiary);
}

.flex-row .card {
  justify-content: space-between;
  padding: 5rem;
}

.flex-row .card a {
  color: var(--tertiary);
}

.social-list__item {
  display: inline;
  justify-content: space-between;
  padding: 5rem;
  position: sticky;
  margin: 5rem;

}
#submit-form {
  background-color: var(--tertiary);
  color: var(--light);
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 5px;
  padding: 0.5rem;
  font-weight: bold;
}

:root {
  --primary: #f05d5e;
  --secondary: #d6e0ec;
  --tertiary: #ca2464;
  --quatrinary: #149597;
  --dark: #36393B;
  --light: #EFF3F7;
  --error: #B44647;
  --spacing-one: calc(1rem * 0.25);
  --spacing-two: calc(1rem * 0.5);
  --spacing-three: 1rem;
  --spacing-four: calc(1rem * 1.5);
  --spacing-five: calc(1rem * 3);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: var(--tertiary-color);
  background-color: var(--secondary);
}

h1 {
  font-size: 2.25rem;
  padding: 1rem;
  text-align: center;
}

h2 {
  font-size: 2rem;
  padding: 1rem;
  text-align: center;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
  text-align: center;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  text-align: center;
}

a:visited {
  color: inherit;
}

li {
  margin-bottom: var(--spacing-one);
  color: var(--dark);
  font-weight: bolder;
  
}

nav a {
  padding-right: var(--spacing-three);
  padding-left: var(--spacing-three);
  transition: all 0.2s;
}

nav a:hover {
  color: var(--tertiary);
}

input, textarea {
  width: 100%;
  font-size: 1rem;
  background: var(--light);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', 'Geneva', 'Verdana', 'sans-serif';
  padding: var(--spacing-two) var(--spacing-one);
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 5px;
  transition: box-shadow 0.2s;
}

input:focus, textarea:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

textarea {
  min-height: 150px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  /* overflow: auto; */
  align-content: center;
}

header {
  background-color: var(--quatrinary);
}

footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  width: 100%;
  background-color: var(--secondary);
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}


.hero-img {
  width: 100%;
  height: auto;
}

.about {
  background-color: var(--tertiary);
  border-radius: 15px;
}

.btn {
  border: 2px solid var(--tertiary);
  border-radius: 30px;
  background-color: var(--tertiary);
  padding: var(--spacing-two) var(--spacing-three);
  color: var(--light);
  font-size: 1.1rem;
}

.btn:hover {
  color: var(--tertiary);
  background-color: var(--secondary);
}

.card {
  background-color: var(--light);
  padding: var(--spacing-four);
  border-radius: 10px;
  box-shadow: rgba(0,0,0,0.3);
}

.project-img {
  width: 100%;
}

